import type { Dispatch } from 'redux'
import { setAppStatusAC } from '../../app/app-reducer'
import { authApi, type LoginParamsType } from '../../api/todolists-api'
import { handleServerAppError, handleServerNetworkError } from '../../utils/error-utils'
import { clearTodosDataAC } from '../TodolistsList/todolists-reduser'
import { createSlice, type PayloadAction } from '@reduxjs/toolkit'

const initialState = {
  isLoggedIn: false,
}
const slice = createSlice({
  name: 'auth',
  initialState,
  reducers: {
    // PayloadAction         
    setIsLoggedInAC(state, action: PayloadAction<{ value: boolean }>) {
      state.isLoggedIn = action.payload.value
    },
  },
})
export const authReduser = slice.reducer
export const { setIsLoggedInAC } = slice.actions
//thunk
export const loginT = (data: LoginParamsType) => {
  return (dispatch: Dispatch) => {
    dispatch(setAppStatusAC({ status: 'loading' }))
    authApi
      .login(data)
      .then(res => {
        console.log(res.data)
        if (res.data.resultCode === 0) {
          dispatch(setIsLoggedInAC({ value: true }))
          dispatch(setAppStatusAC({ status: 'successes' }))
          localStorage.setItem('sn-token', res.data.data.token)
        } else {
          handleServerAppError(res.data, dispatch)
        }
      })
      .catch(error => {
        handleServerNetworkError(error, dispatch)
      })
  }
}
export const logoutTC = () => {
  return (dispatch: Dispatch) => {
    dispatch(setAppStatusAC({ status: 'loading' }))
    authApi
      .logout()
      .then(res => {
        if (res.data.resultCode === 0) {
          dispatch(setIsLoggedInAC({ value: false }))
          dispatch(setAppStatusAC({ status: 'successes' }))
          dispatch(clearTodosDataAC())
          localStorage.removeItem('sn-token')
        } else {
          handleServerAppError(res.data, dispatch)
        }
      })
      .catch(error => {
        handleServerNetworkError(error, dispatch)
      })
  }
}
